* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    flex-wrap: wrap;
    padding: 20px;
}

.card {
    width: 300px;
    height: 300px;
    border-radius: 10px;
    overflow: hidden;
    border: 2px solid;
}

.card img {
    width: 100%;
    height: 200px;
    object-fit: cover;
}

.price {
    font-weight: 800;
}

.brand,
.price {
    margin-top: 10px;
    margin-inline: 10px;
}
